@import 'components';
@import 'layout';


section{
    background-color: #C7A98A;
    border: solid 1px black;
    border-radius: 60px;
    display: flex;
    justify-content: space-around;
    margin: 1%;
    padding: 8%;
    div{
        align-content: center;
        text-align: center;
        width: 200px;
        
    }
    img{
        height: 497px;
        width: 200px;
    }
    article{
        width: 40%;
        display: flex;
        flex-direction: column;
        h1{
            font-size: xx-large;
            text-decoration: underline;
        }
        span{
            font-size: x-large;            

        }
        .bloq{
            background-color: #7D5A35;
            border: solid #7D5A35 1px;
            padding: 1cm;
            width: 20vw;
        }
    }
}

@media(max-width:850px){
section{
    flex-direction: column;
    align-items: center;
    img{
       width: 100px; 
       height: 200px;
    }
    article{
    span{
        font-size: small;
        width: 35vw;    
    }
}
}
}